@import "settings";

section.experience {
  display: flex;
  position: relative;
  margin-bottom: 2.5em;

  @media only screen and (max-width: $mobile-width) {
    text-align: left;
  }

  .locale {
    align-items: baseline;
    display: flex;
    justify-content: space-between;

    @media only screen and (max-width: $mobile-width) {
      flex-direction: column;
    }
  }

  .where {
    font-size: 1.5em;
    font-style: normal;
    margin: 0;
  }

  .when {
    font-size: 1.05em;

    .present {
      font-weight: 500;
    }
  }

  .info {
    width: 100%;
  }

  .image {
    font-size: 3em;
    margin: 0.5rem 2.5rem 0 0;
  }

  .title {
    display: none;
    font-weight: 500;
    margin: 0;
  }

  .description {
    p {
      margin: 0;
    }
  }
}

.experience-heading {
  margin-bottom: 1em;
}
