.post-options {
  .filter-section {
    display: inline-block;
  }

  @media screen and (max-width: 600px) {
    .domains {
      display: inline-flex;
      flex-direction: column;
    }
  }
}

.filter-option {
  line-height: 2em;
  margin-right: 0.75em;
  vertical-align: middle;

  input,
  label {
    vertical-align: middle;
  }

  &:last-of-type {
    margin-right: 0;
  }
}

select[name*="-filter"] {
  margin-right: 1.5em;
}
